// Amazon-style product reviews data structure
export interface Review {
  id: string;
  productId: string;
  userId: string;
  userName: string;
  userAvatar?: string;
  rating: number; // 1-5 stars
  title: string;
  comment: string;
  dateCreated: Date;
  verified: boolean; // Verified purchase
  helpful: number; // Number of helpful votes
  notHelpful: number; // Number of not helpful votes
  images?: string[]; // Customer uploaded images
  vineCustomer?: boolean; // Amazon Vine customer
  earlyReviewer?: boolean; // Early reviewer program
  topReviewer?: boolean; // Top reviewer badge
}

export interface ReviewSummary {
  averageRating: number;
  totalReviews: number;
  ratingDistribution: { [key: number]: number };
  recommendationPercentage: number;
}

