'use client';

import { useState, useMemo } from 'react';
import Image from 'next/image';
import StarRating, { CompactRating, RatingBreakdown } from './StarRating';
import WriteReviewForm from './WriteReviewForm';
import { 
  Review, 
  ReviewSortOption,
  getReviewsForProduct, 
  getReviewSummary,
  sortReviews,
  getHelpfulnessRatio 
} from '@/data/reviews';
import { products } from '@/data/products';

interface ReviewsProps {
  productId: string;
}

export default function Reviews({ productId }: ReviewsProps) {
  const [sortBy, setSortBy] = useState<ReviewSortOption>('helpful');
  const [filterRating, setFilterRating] = useState<number | null>(null);
  const [showWriteReview, setShowWriteReview] = useState(false);
  const [expandedReviews, setExpandedReviews] = useState<Set<string>>(new Set());
  const [helpfulVotes, setHelpfulVotes] = useState<{ [key: string]: 'helpful' | 'not-helpful' | null }>({});

  const allReviews = getReviewsForProduct(productId);
  const reviewSummary = getReviewSummary(productId);
  const product = products.find(p => p.id === productId);

  // Filter and sort reviews
  const processedReviews = useMemo(() => {
    let filtered = allReviews;
    
    if (filterRating) {
      filtered = allReviews.filter(review => review.rating === filterRating);
    }
    
    return sortReviews(filtered, sortBy);
  }, [allReviews, filterRating, sortBy]);

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }).format(new Date(date));
  };

  const toggleReviewExpansion = (reviewId: string) => {
    const newExpanded = new Set(expandedReviews);
    if (newExpanded.has(reviewId)) {
      newExpanded.delete(reviewId);
    } else {
      newExpanded.add(reviewId);
    }
    setExpandedReviews(newExpanded);
  };

  const handleHelpfulVote = (reviewId: string, voteType: 'helpful' | 'not-helpful') => {
    setHelpfulVotes(prev => ({
      ...prev,
      [reviewId]: prev[reviewId] === voteType ? null : voteType
    }));
  };

  const truncateText = (text: string, maxLength: number = 300) => {
    if (text.length <= maxLength) return text;
    return text.substring(0, maxLength) + '...';
  };

  if (allReviews.length === 0) {
    return (
      <div className="bg-white">
        <div className="border-b border-gray-200 pb-4 mb-6">
          <h2 className="text-2xl font-bold text-gray-900">Customer Reviews</h2>
        </div>
        
        <div className="text-center py-12">
          <div className="flex justify-center mb-4">
            <StarRating rating={0} size="xl" />
          </div>
          <h3 className="text-xl font-medium text-gray-900 mb-2">No customer reviews</h3>
          <p className="text-gray-600 mb-6">Be the first to share your thoughts on this product</p>
          <button
            onClick={() => setShowWriteReview(true)}
            className="bg-orange-500 hover:bg-orange-600 text-white px-6 py-2 rounded-lg font-medium transition-colors"
          >
            Write a customer review
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white">
      {/* Header */}
      <div className="border-b border-gray-200 pb-4 mb-6">
        <h2 className="text-2xl font-bold text-gray-900">Customer Reviews</h2>
      </div>

      {/* Review Summary */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 mb-8">
        {/* Overall Rating */}
        <div className="lg:col-span-1">
          <div className="text-center lg:text-left">
            <div className="flex items-center justify-center lg:justify-start space-x-3 mb-3">
              <span className="text-5xl font-bold text-gray-900">
                {reviewSummary.averageRating.toFixed(1)}
              </span>
              <div>
                <StarRating rating={reviewSummary.averageRating} size="lg" />
                <p className="text-sm text-gray-600 mt-1">
                  {reviewSummary.totalReviews.toLocaleString()} global ratings
                </p>
              </div>
            </div>
            
            <div className="mt-4 p-4 bg-gray-50 rounded-lg">
              <div className="flex items-center justify-between text-sm">
                <span className="font-medium text-gray-700">
                  {reviewSummary.recommendationPercentage}% of customers recommend this product
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* Rating Distribution */}
        <div className="lg:col-span-2">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Rating breakdown</h3>
          <RatingBreakdown 
            distribution={reviewSummary.ratingDistribution} 
            totalReviews={reviewSummary.totalReviews} 
          />
        </div>
      </div>

      {/* Write Review Button */}
      <div className="mb-6">
        <button
          onClick={() => setShowWriteReview(true)}
          className="bg-orange-500 hover:bg-orange-600 text-white px-6 py-2 rounded-lg font-medium transition-colors"
        >
          Write a customer review
        </button>
      </div>

      {/* Filter and Sort Controls */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-6 py-4 border-t border-b border-gray-200">
        {/* Sort */}
        <div className="flex items-center space-x-3">
          <span className="font-medium text-gray-700">Sort by:</span>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value as ReviewSortOption)}
            className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
          >
            <option value="helpful">Top reviews</option>
            <option value="newest">Most recent</option>
            <option value="oldest">Oldest</option>
            <option value="highest">Highest rating</option>
            <option value="lowest">Lowest rating</option>
            <option value="verified">Verified purchases only</option>
          </select>
        </div>

        {/* Filter by Rating */}
        <div className="flex items-center space-x-2">
          <span className="font-medium text-gray-700">Filter by stars:</span>
          <div className="flex space-x-1">
            <button
              onClick={() => setFilterRating(null)}
              className={`px-3 py-1 text-sm rounded-full transition-colors ${
                filterRating === null
                  ? 'bg-orange-500 text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              All
            </button>
            {[5, 4, 3, 2, 1].map((rating) => (
              <button
                key={rating}
                onClick={() => setFilterRating(rating === filterRating ? null : rating)}
                className={`px-3 py-1 text-sm rounded-full transition-colors ${
                  filterRating === rating
                    ? 'bg-orange-500 text-white'
                    : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                }`}
              >
                {rating}‚òÖ
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Reviews List */}
      <div className="space-y-6">
        {processedReviews.map((review) => {
          const isExpanded = expandedReviews.has(review.id);
          const userVote = helpfulVotes[review.id];
          const helpfulnessRatio = getHelpfulnessRatio(review);
          const shouldTruncate = review.comment.length > 300;

          return (
            <div key={review.id} className="border-b border-gray-200 pb-6 last:border-b-0">
              {/* Review Header */}
              <div className="flex items-start space-x-4 mb-3">
                {/* User Avatar */}
                <div className="w-8 h-8 bg-gray-300 rounded-full flex items-center justify-center flex-shrink-0">
                  {review.userAvatar ? (
                    <Image
                      src={review.userAvatar}
                      alt={review.userName}
                      width={32}
                      height={32}
                      className="rounded-full"
                    />
                  ) : (
                    <span className="text-sm font-medium text-gray-600">
                      {review.userName.charAt(0).toUpperCase()}
                    </span>
                  )}
                </div>

                <div className="flex-1">
                  <div className="flex items-center space-x-2 mb-1">
                    <span className="font-medium text-gray-900">{review.userName}</span>
                    {review.topReviewer && (
                      <span className="bg-orange-100 text-orange-800 text-xs px-2 py-1 rounded-full font-medium">
                        Top Reviewer
                      </span>
                    )}
                    {review.vineCustomer && (
                      <span className="bg-green-100 text-green-800 text-xs px-2 py-1 rounded-full font-medium">
                        Vine Customer
                      </span>
                    )}
                    {review.earlyReviewer && (
                      <span className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full font-medium">
                        Early Reviewer
                      </span>
                    )}
                  </div>
                  
                  <div className="flex items-center space-x-3">
                    <StarRating rating={review.rating} size="sm" />
                    <span className="text-sm font-semibold text-gray-900">{review.title}</span>
                  </div>
                  
                  <div className="flex items-center space-x-2 mt-1 text-sm text-gray-600">
                    <span>Reviewed on {formatDate(review.dateCreated)}</span>
                    {review.verified && (
                      <>
                        <span>‚Ä¢</span>
                        <span className="text-orange-600 font-medium">Verified Purchase</span>
                      </>
                    )}
                  </div>
                </div>
              </div>

              {/* Review Content */}
              <div className="ml-12">
                <p className="text-gray-700 leading-relaxed mb-3">
                  {shouldTruncate && !isExpanded 
                    ? truncateText(review.comment)
                    : review.comment
                  }
                  {shouldTruncate && (
                    <button
                      onClick={() => toggleReviewExpansion(review.id)}
                      className="text-blue-600 hover:text-blue-700 hover:underline ml-1 font-medium"
                    >
                      {isExpanded ? 'Show less' : 'Read more'}
                    </button>
                  )}
                </p>

                {/* Review Images */}
                {review.images && review.images.length > 0 && (
                  <div className="flex space-x-2 mb-4">
                    {review.images.map((image, index) => (
                      <div key={index} className="relative w-20 h-20 rounded-lg overflow-hidden border border-gray-200">
                        <Image
                          src={image}
                          alt={`Review image ${index + 1}`}
                          fill
                          className="object-cover hover:scale-110 transition-transform cursor-pointer"
                        />
                      </div>
                    ))}
                  </div>
                )}

                {/* Helpful Voting */}
                <div className="flex items-center space-x-4 text-sm">
                  <div className="flex items-center space-x-2">
                    <span className="text-gray-600">Helpful?</span>
                    <button
                      onClick={() => handleHelpfulVote(review.id, 'helpful')}
                      className={`px-3 py-1 rounded-full border transition-colors ${
                        userVote === 'helpful'
                          ? 'bg-blue-50 border-blue-300 text-blue-700'
                          : 'border-gray-300 text-gray-700 hover:bg-gray-50'
                      }`}
                    >
                      üëç Yes ({review.helpful})
                    </button>
                    <button
                      onClick={() => handleHelpfulVote(review.id, 'not-helpful')}
                      className={`px-3 py-1 rounded-full border transition-colors ${
                        userVote === 'not-helpful'
                          ? 'bg-red-50 border-red-300 text-red-700'
                          : 'border-gray-300 text-gray-700 hover:bg-gray-50'
                      }`}
                    >
                      üëé No ({review.notHelpful})
                    </button>
                  </div>
                  
                  {helpfulnessRatio > 0 && (
                    <span className="text-gray-500">
                      {helpfulnessRatio}% found this helpful
                    </span>
                  )}
                  
                  <button className="text-gray-500 hover:text-gray-700 transition-colors">
                    Report
                  </button>
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {/* Show more reviews if there are many */}
      {processedReviews.length > 5 && (
        <div className="mt-8 text-center">
          <button className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-6 py-2 rounded-lg font-medium transition-colors">
            See more reviews
          </button>
        </div>
      )}

      {/* Write Review Modal */}
      {showWriteReview && (
        <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-lg max-w-lg w-full p-6">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-xl font-bold text-gray-900">Write a Customer Review</h3>
              <button
                onClick={() => setShowWriteReview(false)}
                className="text-gray-400 hover:text-gray-600 text-2xl"
              >
                √ó
              </button>
            </div>
            
            <div className="text-center py-8">
              <div className="mb-4">
                <StarRating rating={0} size="lg" interactive />
              </div>
              <p className="text-gray-600 mb-6">
                Review submission functionality coming soon! We're working on implementing a comprehensive review system.
              </p>
              <div className="space-y-3">
                <button
                  onClick={() => setShowWriteReview(false)}
                  className="w-full bg-gray-600 hover:bg-gray-700 text-white px-4 py-2 rounded-lg transition-colors"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
