'use client';

import { useState } from 'react';

interface StarRatingProps {
  rating: number;
  maxRating?: number;
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
  interactive?: boolean;
  showRating?: boolean;
  showOutOf?: boolean;
  onChange?: (rating: number) => void;
  className?: string;
}

export default function StarRating({
  rating,
  maxRating = 5,
  size = 'md',
  interactive = false,
  showRating = false,
  showOutOf = false,
  onChange,
  className = ''
}: StarRatingProps) {
  const [hoverRating, setHoverRating] = useState(0);
  const [isHovering, setIsHovering] = useState(false);

  const sizes = {
    xs: 'w-3 h-3',
    sm: 'w-4 h-4',
    md: 'w-5 h-5',
    lg: 'w-6 h-6',
    xl: 'w-8 h-8'
  };

  const textSizes = {
    xs: 'text-xs',
    sm: 'text-sm',
    md: 'text-base',
    lg: 'text-lg',
    xl: 'text-xl'
  };

  const displayRating = isHovering && interactive ? hoverRating : rating;

  const handleStarClick = (starRating: number) => {
    if (interactive && onChange) {
      onChange(starRating);
    }
  };

  const handleStarHover = (starRating: number) => {
    if (interactive) {
      setHoverRating(starRating);
      setIsHovering(true);
    }
  };

  const handleMouseLeave = () => {
    if (interactive) {
      setIsHovering(false);
      setHoverRating(0);
    }
  };

  const getStarFill = (starIndex: number): string => {
    const diff = displayRating - starIndex;
    
    if (diff >= 1) {
      // Full star
      return '#FCD34D'; // Amazon yellow
    } else if (diff >= 0.5) {
      // Half star
      return 'url(#half-star-gradient)';
    } else {
      // Empty star
      return '#E5E7EB'; // Gray
    }
  };

  const renderStars = () => {
    return Array.from({ length: maxRating }, (_, index) => {
      const starIndex = index + 1;
      const fillColor = getStarFill(starIndex);
      
      return (
        <button
          key={starIndex}
          type="button"
          className={`${sizes[size]} ${
            interactive 
              ? 'cursor-pointer hover:scale-110 transition-all duration-150 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:ring-opacity-50 rounded' 
              : 'cursor-default'
          } ${className}`}
          onClick={() => handleStarClick(starIndex)}
          onMouseEnter={() => handleStarHover(starIndex)}
          onMouseLeave={handleMouseLeave}
          disabled={!interactive}
          aria-label={`${starIndex} star${starIndex !== 1 ? 's' : ''}`}
        >
          <svg
            className="w-full h-full"
            fill={fillColor}
            viewBox="0 0 24 24"
            stroke={interactive && isHovering && hoverRating >= starIndex ? '#F59E0B' : 'none'}
            strokeWidth={interactive && isHovering && hoverRating >= starIndex ? 1 : 0}
          >
            <defs>
              <linearGradient id="half-star-gradient" x1="0%" y1="0%" x2="100%" y2="0%">
                <stop offset="50%" stopColor="#FCD34D" />
                <stop offset="50%" stopColor="#E5E7EB" />
              </linearGradient>
            </defs>
            <path d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z" />
          </svg>
        </button>
      );
    });
  };

  return (
    <div className="flex items-center space-x-1">
      <div 
        className="flex items-center space-x-0.5"
        onMouseLeave={handleMouseLeave}
      >
        {renderStars()}
      </div>
      
      {(showRating || showOutOf) && (
        <span className={`ml-2 ${textSizes[size]} text-gray-600 font-medium`}>
          {showRating && (
            <span className="text-gray-800">
              {displayRating.toFixed(1)}
            </span>
          )}
          {showOutOf && (
            <span className="text-gray-500">
              {showRating ? ' out of ' : ''}{maxRating}
            </span>
          )}
        </span>
      )}
      
      {interactive && isHovering && (
        <span className={`ml-2 ${textSizes[size]} text-orange-600 font-semibold`}>
          {hoverRating} star{hoverRating !== 1 ? 's' : ''}
        </span>
      )}
    </div>
  );
}

// Compact rating display for product cards
export function CompactRating({ 
  rating, 
  reviewCount, 
  size = 'sm' 
}: { 
  rating: number; 
  reviewCount: number; 
  size?: 'xs' | 'sm' | 'md' 
}) {
  if (reviewCount === 0) {
    return (
      <div className="flex items-center space-x-1">
        <StarRating rating={0} size={size} />
        <span className={`${size === 'xs' ? 'text-xs' : 'text-sm'} text-gray-500`}>
          No reviews
        </span>
      </div>
    );
  }

  return (
    <div className="flex items-center space-x-1">
      <StarRating rating={rating} size={size} />
      <span className={`${size === 'xs' ? 'text-xs' : 'text-sm'} text-gray-600 hover:text-orange-600 cursor-pointer transition-colors`}>
        {rating.toFixed(1)} ({reviewCount.toLocaleString()})
      </span>
    </div>
  );
}

// Rating breakdown component for Amazon-style distribution
export function RatingBreakdown({ 
  distribution, 
  totalReviews 
}: { 
  distribution: { [key: number]: number }; 
  totalReviews: number; 
}) {
  const getRatingPercentage = (rating: number): number => {
    if (totalReviews === 0) return 0;
    return Math.round((distribution[rating] / totalReviews) * 100);
  };

  return (
    <div className="space-y-2">
      {[5, 4, 3, 2, 1].map((rating) => {
        const percentage = getRatingPercentage(rating);
        const count = distribution[rating] || 0;
        
        return (
          <div key={rating} className="flex items-center space-x-3 group cursor-pointer hover:bg-gray-50 p-1 rounded transition-colors">
            <button className="flex items-center space-x-1 text-sm text-blue-600 hover:text-blue-700 hover:underline">
              <span className="font-medium">{rating} star</span>
            </button>
            
            <div className="flex-1 h-4 bg-gray-200 rounded-full overflow-hidden">
              <div 
                className="h-full bg-gradient-to-r from-orange-400 to-orange-500 transition-all duration-500 ease-out"
                style={{ width: `${percentage}%` }}
              />
            </div>
            
            <span className="text-sm text-blue-600 hover:text-blue-700 hover:underline w-8">
              {percentage}%
            </span>
          </div>
        );
      })}
    </div>
  );
}
